@font-face {
  font-family: 'TT Lakes';
  src: url('../../src/fonts/TTLakes-Medium.eot');
  src: local('../../src/fonts/TT Lakes Medium'), local('TTLakes-Medium'),
  url('../../src/fonts/TTLakes-Medium.eot?#iefix') format('embedded-opentype'),
  url('../../src/fonts/TTLakes-Medium.woff') format('woff'),
  url('../../src/fonts/TTLakes-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}


@font-face {
  font-family: 'TT Lakes';
  src: url('../../src/fonts/TTLakes-Regular.eot');
  src: local('../../src/fonts/TT Lakes Regular'), local('TTLakes-Regular'),
  url('../../src/fonts/TTLakes-Regular.eot?#iefix') format('embedded-opentype'),
  url('../../src/fonts/TTLakes-Regular.woff') format('woff'),
  url('../../src/fonts/TTLakes-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'TT Lakes';
  src: url('../../src/fonts/TTLakes-Bold.eot'),
  url('../../src/fonts/TTLakes-Bold.eot?#iefix') format('embedded-opentype'),
  url('../../src/fonts/TTLakes-Bold.woff') format('woff'),
  url('../../src/fonts/TTLakes-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
